.question {
    &__title {
      text-align: center;
      font-size: 38px;
    }
    &__title::after {
      content: '';
      display: block;
      background: var(--stripe-color);
      height: 2px;
      margin: 28px auto 36px;
      width: 50px;
    }
    &__desc {
      display: flex;
      justify-content: center;
      color: var(--secondary-text);
      line-height: 26.91px;
      margin-bottom: 36px;
    }
    &__watch {
      display: flex;
      justify-content: center;
      margin-top: 34px;
    }
    .look {
      &__button {
        cursor: pointer;
        width: 215px;
        height: 56px;
        text-align: center;
        background-color: transparent;
        color: var(--secondary-text);
        border: 1px solid var(--secondary-text);
        border-radius: 3px;
        font-size: 18px;
        transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);

        @media screen and (min-width: 320px) {
          border-radius: 20px;
        }
        @media screen and (min-width: 768px) {
          border-radius: 20px;
        }
        @media screen and (min-width: 1200px) {
          border-radius: 6px
        }
      }
      &__button:hover,
      &__button:focus {
        color: var(--footer-bg);
        border-color: var(--footer-bg);
      }
    }
  }